import { Model } from '../Model/model';
import { getOAuth2Client } from "../Auth/Auth";
import { OAuth2Client } from 'google-auth-library';

async function getDataFileId(oAuth2Client: OAuth2Client): Promise<string> {
  var fileId = window.localStorage["NotesFileId"];

  if (fileId != null) return fileId;

  /* get parent folder */

  const parentFolderQuery = new URLSearchParams({
    q: "'root' in parents and mimeType = 'application/vnd.google-apps.folder' and name = 'notes-perso-boussole'",
    fields: "files/id",
  });

  const parentFolderResponse = await oAuth2Client.request({ url: `https://www.googleapis.com/drive/v3/files?${parentFolderQuery}` });
  var parentFolderId = (parentFolderResponse.data as any).files[0].id;

  /* get file id */

  const fileQuery = new URLSearchParams({
    q: `'${parentFolderId}' in parents and name = 'notes.json'`,
    fields: "files/id",
  });

  const fileResponse = await oAuth2Client.request({ url: `https://www.googleapis.com/drive/v3/files?${fileQuery}` });
  window.localStorage["NotesFileId"] = fileId = (fileResponse.data as any).files[0].id;

  return fileId;
}

export async function getData() {
  const oAuth2Client = getOAuth2Client();
  const fileId = await getDataFileId(oAuth2Client);
  var fileContent = await oAuth2Client.request({
    url: `https://www.googleapis.com/drive/v3/files/${fileId}?alt=media`,
  });
  return fileContent.data as Omit<Model, "dataStorage">;
}

export async function saveData(data: Omit<Model, "dataStorage">) {
  const oAuth2Client = getOAuth2Client();
  const fileId = await getDataFileId(oAuth2Client);
  await oAuth2Client.request({
    url: `https://www.googleapis.com/upload/drive/v3/files/${fileId}`,
    method: "PATCH",
    data: JSON.stringify(data, null, 1),
  });
}
