import { Reducer } from "react"
import { Model } from "../Model/model"
import { StoreAction, Action } from "./store"
import { useDispatch, useSelector } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { getData, saveData } from "../Services/DataStore";

// *** Model ***

export interface DataStorageModel {
  status: "fetching" | "saving" | "inactive";
  fetchError?: any;
  saveError?: any;
}

// *** Actions ***

interface ModelFetchingAction extends Action {
  type: "ModelFetching";
}

interface ModelFetchedAction extends Action {
  type: "ModelFetched";
  model: Omit<Model, "dataStorage">;
}

interface ModelFetchErrorAction extends Action {
  type: "ModelFetchError";
  error: any;
}

interface ModelSavingAction extends Action {
  type: "ModelSaving";
}

interface ModelSavedAction extends Action {
  type: "ModelSaved";
}

interface ModelSaveErrorAction extends Action {
  type: "ModelSaveError";
  error: any;
}

export type DataStorageActions =
  ModelFetchingAction |
  ModelFetchedAction |
  ModelFetchErrorAction |
  ModelSavingAction |
  ModelSavedAction |
  ModelSaveErrorAction;

// *** Reducers ***

export const dataStorageReducer: Reducer<Model, StoreAction> = (model, action): Model => {
  if (model == undefined) throw new Error("dataStorageReducer: Unexpected undefined model.");

  switch (action.type) {
    case "ModelFetching": return { ...model, dataStorage: { ...model.dataStorage, status: "fetching", fetchError: undefined } };
    case "ModelFetched": return { ...action.model, dataStorage: { ...model.dataStorage, status: "inactive", fetchError: undefined } };
    case "ModelFetchError": return { ...model, dataStorage: { ...model.dataStorage, status: "inactive", fetchError: action.error } };
    case "ModelSaving": return { ...model, dataStorage: { ...model.dataStorage, status: "saving", saveError: undefined } };
    case "ModelSaved": return { ...model, dataStorage: { ...model.dataStorage, status: "inactive", saveError: undefined } };
    case "ModelSaveError": return { ...model, dataStorage: { ...model.dataStorage, status: "inactive", saveError: action.error } };
    default: return model;
  }
}

// *** Service ***

export const useDataStorage = () => useSelector<Model, DataStorageModel>(model => model.dataStorage);

export function useFetchData() {
  const thunkDispatch = useDispatch<ThunkDispatch<Model, undefined, StoreAction>>();

  return () => thunkDispatch(async (dispatch, getState) => {
    const state = getState();
    if (state.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelFetching" });

    try {
      const model = await getData();
      dispatch({ type: "ModelFetched", model });
    }
    catch (error) {
      dispatch({ type: "ModelFetchError", error });
    }
  });
}

export function useSaveData() {
  const thunkDispatch = useDispatch<ThunkDispatch<Model, undefined, StoreAction>>();

  return () => thunkDispatch(async (dispatch, getState) => {
    const state = getState();
    if (state.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelSaving" });

    try {
      const model = await saveData(state);
      dispatch({ type: "ModelSaved", model });
    }
    catch (error) {
      dispatch({ type: "ModelSaveError", error });
    }
  });
}
