import { createStore as reduxCreateStore, combineReducers, Reducer, applyMiddleware } from 'redux'
import thunk from 'redux-thunk';
import { TaskActions, taskReducer } from './tasks'
import { Model } from '../Model/model';
import { DataStorageActions, dataStorageReducer } from './dataStorage';

export interface Action {
  type: string;
}

export type StoreAction = TaskActions | DataStorageActions;

const combinedReducers: Reducer<Model, StoreAction> = combineReducers<Model, StoreAction>({
  tasks: taskReducer,
  dataStorage: model => model || { status: "inactive" },
});

const reducer: Reducer<Model, StoreAction> = (model, action) => {
  model = combinedReducers(model, action);
  model = dataStorageReducer(model, action);
  return model;
}

export const createStore = () => {
  return reduxCreateStore(reducer, applyMiddleware(thunk));
}
