import React from "react";
import './AuthStatus.css';
import { AuthState } from "../../Auth/AuthState";
import { authenticate } from "../../Auth/Auth";

function getExpirationString(expiration: Date): string {
  var secondsToExpiration = Math.round((expiration.getTime() - new Date().getTime()) / 1000);
  if (secondsToExpiration > 100) {
    return `${Math.round(secondsToExpiration / 60)} minutes`;
  } else {
    return `${Math.round(secondsToExpiration)} seconds`;
  }
}

export function AuthStatus(props: { authState: AuthState }) {
  const { authState } = props;
  const expiration = authState.status == 'Authenticated' ? authState.expiration : null;

  const [timeLeft, setTimeLeft] = React.useState(expiration === null ? null : getExpirationString(expiration));

  React.useEffect(() => {
    if (expiration === null) {
      return;
    }

    const intervalId = setInterval(() => {
      setTimeLeft(getExpirationString(expiration));
    }, 1000);

    return () => clearInterval(intervalId);
  }, [expiration]);

  if (expiration === null) {
    return <div>Not authenticated.</div>
  }

  return <div className="authStatus">
    {authState.status}, expiration: {timeLeft}
    <span className="authRenewButton" onClick={authenticate}>Renew</span>
  </div>;
}
