import React, { useState, useMemo } from "react";
import "./Tasks.css";
import { Task, TaskNoId } from "../../Model/model";
import { Button } from "../Utility/Button/Button";
import { useTaskService as useTaskService } from "../../Store/tasks";

export type TaskFormProps = {
  task: Task | undefined;
  onClose: () => void;
};

export function TaskForm(props: TaskFormProps) {
  const id = useMemo(() => props.task ? props.task.id : undefined, [props.task]);
  const [editedTask, setEditedTask] = useState<TaskNoId>(props.task || { "title": "" });
  const { createTask, updateTask } = useTaskService();

  const onSave = () => {
    if (id) {
      updateTask(id, editedTask);
    } else {
      createTask(editedTask);
    }
    props.onClose();
  }

  return <div className="taskForm">
    Titre<br />
    <input type="text" value={editedTask.title} onChange={event => setEditedTask({ title: event.target.value })} />
    <div>
      <Button onClick={onSave}>Enregistrer</Button>
      <Button onClick={props.onClose}>Fermer</Button>
    </div>
  </div>;
}
